<template>
	<div class="main">
	    <div class="block"></div>
		<div class="container" v-for="data in news">
			<titleNavigation titleName="新闻"></titleNavigation>
			<div style="font-size:12px;margin-top:16px auto;color:#656460;padding-bottom: 14px;text-align:center;padding-top:13px;">来源:育灵童　
						|　作者:育灵童　						|　发布时间：<span v-html="format(data.time)"></span></div>
			<p>{{data.content}}</p>
		</div>
	</div>
</template>

<script>
import axios from 'axios';
import '@/js/zepto.min.js';
import titleNavigation from '@/components/titleNavigation'
import newsItem from '@/components/newsItem';

 export default{
 	name:'index',
 	data(){
 		return {
 			id: this.$route.query.id,//id
 			news:[],
 		}
 	},
 	created(){
 		axios.get('https://www.easy-mock.com/mock/5a0bad8922eff44db04955c0/example_1510714761014/news',$.param({id : this.id})).then(response=>{
 			this.news = response.data.data.news;
 		})
 	},
 	methods:{
 		format : function(time) {
 			var date = new Date(time);
 			function two(day) {
 				if( day<10 ) {
 					return '0' + day;
 				}
 				return day;
 			}
 			return  date.getFullYear()+ '-' + two(date.getMonth()+1) + '-' +
 			two(date.getDate());
 		},
 	},
 	components: {
 	  titleNavigation,
 	},

 }
</script>

<style scoped>
	.main{
		width:100%;
		background:#f1f1f1;
	}
	.block{
		width:100%;
		height:20px;
		background:#f1f1f1;
	}
	.container{
		width:1200px;
		margin:0px auto;
		background:#ffffff;
	}
	.container p{
		padding:30px 176px;
	}
	
</style>


